import React from 'react';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, ListItem, Avatar} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

const Stats = (props,go,fetchedUser,profilename,profilesurname,profileimg) => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Статистика
		</PanelHeader>
			<Group title="Ваша статистика">
			<ListItem
				before={ <Avatar src={props.profileimg}/> }
				description={'Первый запуск: '+props.firstopen}
			>
			{ props.profilename  } { props.profilesurname  }
			</ListItem>
			</Group>
			<Group>
			<ListItem>
				<div>Ваш индификатор: { props.vkid  }</div>
				<div>Рекорд за все время: { props.record }</div>
			</ListItem>
			</Group>
	</Panel>

);


export default Stats;
